.post-wrapper{
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row wrap;
}


.post{
  padding: 10px;
  background: #ccc;
  margin: 10px;
 
 width:22%;
  & h2{
    font-size: 18px;
  }
  & img{
    max-width: 100%;
  }
}
.categories{
  list-style: none;
}
.category-item{
  display: inline-block;
  padding: 3px;
  border-radius: 5px;
  background: #fff;
  margin: 5px;
}

.banner{
  height: 200px;
  background:url('http://cdn.ebaumsworld.com/mediaFiles/picture/2047837/82964655.jpg') 50% no-repeat;
  background-size: cover;
}
.filters{
  background: #ccd;
  padding: 20px;
}
.btn-container{
  padding: 20px;
  background: #cdc;
  .open{
      text-decoration: none;
      padding: 10px;
      text-transform: uppercase;
      background-color: lightgreen;
      color: #fff;
  }
  .cl{
 text-decoration: none;
      padding: 10px;
      text-transform: uppercase;
      background-color: darkred;
      color: #fff;
  }
}
.overlay{
  position: fixed;
  top: 0;bottom: 0;
  right: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 22;
}
.single-preview{
  position: fixed;
  top: 0;bottom: 0;
  right: 0;
  width: 50%;
  background-color: #fff;
  padding: 30px;
  z-index: 222;
  img{
    max-width: 80%;
    display: block;
    margin: 0 auto;
  }
  .post-nav{
    display: block;
    height: 30px;
    width: 30px;
    padding: 15px;
    border-radius: 50%; 
    background-color: #ccc;
    color: #fff;
    text-decoration: none;
    font-weight: 700 ;
  }
  .prev{
    position: absolute;
    top:calc( 50% - 70px);
    left: 10px;
  }
  .next{
  position: absolute;
  top:calc( 50% - 70px);
    right: 10px;
  }

  .cls-btn{
    position: absolute;
    height: 30px;
    width: 30px;
    color: #000;
    background-color: red;
    top: 40px;
    left: -30px;
  }
}
.post-single{
  background-color: #ccc;
}
.post-content{
  max-width: 70%;
  margin: auto;
  background-color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  img{
    display: block;
    max-width: 90%;
    margin: auto;
    padding: 10px;
  }
}  
.btn-container{
  position: relative;
  height: 50px;
}
.read-more{
    text-decoration: none;
    text-transform: uppercase;
    font-size: 18px;
    padding: 15px;
    background-color: mediumseagreen;
    color: #fff;
    position: absolute;
    left: 50%;
    tanslateX: -50%;
  }
  .next-post-link{
    float: right;
     text-decoration: none;
    text-transform: uppercase;
    font-size: 18px;
    padding: 15px;
    background-color: salmon;
    color: #fff;
  }
  .prev-post-link{
     float: left;
      text-decoration: none;
    text-transform: uppercase;
    font-size: 18px;
    padding: 15px;
    background-color: salmon;
    color: #fff;
  }